@import url('https://fonts.googleapis.com/css2?family=Poppins&family=Russo+One&display=swap');
body{
    background: white;
    color : black;
    font-family: Poppins , "Russo One";
}

.mytodos .title{
    text-align: center;
    word-spacing: 10px;
    letter-spacing: 2px;
    font-size : 30px;
    text-decoration :line-through;
}


.todoform , .todolist{
    display: flex;
    justify-content: center;
    align-items: center;
}

.todoform button{
    position: absolute;
    right : 30%;
    outline: none;
    border : none;
    border-radius: 50%;
    box-shadow: 1px 1px 10px grey;
    height : 30px;
    width: 30px;
}

.todoform span{
    font-size : 20px;
    color : orange;
    display: flex;
    align-items: center;
    justify-content: center;
}

.todoform .input{
    margin : 20px auto;
    padding : 5px 15px;
    font-family: Poppins;
    width : 40%;
    height : 35px;
    outline : none;
    border : none;
    border-radius: 5px;
    border : 1px solid grey;
    font-size: 16px;
}



.todolist ul{ 
    width : 41.5%;
    padding: 0px;
    margin : 0px;
}
.todolist li{
    
    padding : 5px 15px;
    color: black;
    font-size : 20px;
    font-family : Poppins;
    list-style: none;
    margin-bottom: 3px;
    border-left : 3px solid orange;
    background: rgba(128, 128, 128,0.3);
}
.todolist li:last-child{
    margin-bottom: 0px;
}

@media screen and (max-width : 600px){
    .mytodos .title{
        text-align: center;
        word-spacing: 5px;
        letter-spacing: 1px;
        font-size : 20px;
    }
    .todoform .input{
        width : 62%;
        height : 30px;
        border-radius: 5px;
        border : 1px solid grey;
        font-size: 14px;
    }
    .todolist ul{ 
        width : 70%;
        padding: 0px;
        margin : 0px;
        
    }
    .todolist li{
        font-size: 14px;
        
        padding : 5px;
    }
    .todoform button{
        position: absolute;
        right : 18%;
        height : 25px;
        width: 25px;
        border-radius: 0%;
    }
    
}